.hero {
  width: 100%;
  height: 90vh;
  background: linear-gradient(rgba(0, 119, 182, 0.7), rgba(0, 180, 216, 0.7)),
    url("https://picsum.photos/seed/hero/1600/900"); /* ØµÙˆØ±Ø© Ø®Ù„ÙÙŠØ© */
  background-size: cover;
  background-position: center;
  color: var(--text-light);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 190px;
}

.hero .hero-text h1 {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
  color: var(--text-light);
}

.hero .hero-text p {
  font-size: 18px;
  margin-bottom: 30px;
  color: #e5e5e5;
}

.hero .hero-cards {
  display: flex;
  justify-content: center;
  gap: 20px;
  position: relative;
  align-items: center;
}

.hero .hero-cards .card {
  width: 200px;
  height: 280px;
  border-radius: 16px;
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  transition: transform 0.3s;
  cursor: pointer;
}

.hero .hero-cards .card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero .hero-cards .card:nth-child(1) {
  transform: rotate(-6deg);
}
.hero .hero-cards .card:nth-child(2) {
  transform: rotate(6deg);
}
.hero .hero-cards .card:nth-child(3) {
  width: 220px;
  height: 320px;
  position: absolute;
  top: -20px;
  z-index: 1;
  border-radius: 20px;
  transform: rotate(0deg);
}

.hero .hero-cards .card:hover {
  transform: scale(1.05) rotate(0deg);
}

/* Ø§Ù„Ø£Ø²Ø±Ø§Ø± */
.btn {
  margin-right: 20px;
  padding: 10px 20px;
  color: var(--text-light);
  background-color: var(--secondary);
  border-radius: 8px;
  font-weight: bold;
  transition: all 0.3s ease;
}

.btn:hover {
  background-color: var(--highlight);
  transform: translateY(-3px);
}

.btn-1 {
  color: var(--primary);
  background-color: var(--text-light);
}

.btn-2 {
  color: var(--text-light);
  background-color: var(--secondary);
}

.btn-2:hover {
  background-color: var(--highlight);
}

/* Responsive */
@media (max-width: 767px) {
  .hero {
    height: 70vh;
    flex-direction: column;
    text-align: center;
    gap: 40px;
  }

  .hero .hero-cards {
    display: none;
  }
}
