.section-head {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 90px 30px;
  gap: 20px;
}

.title {
  font-size: 28px;
  font-weight: bold;
  color: var(--primary);
}

.subtitle {
  font-weight: 400;
  color: var(--text-dark);
}

.actions > * {
  margin-left: 15px;
  padding: 8px 12px;
  border-radius: 10px;
  border: 1px solid #ddd;
  transition: all 0.3s ease;
}

.actions input:focus,
.actions select:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 6px rgba(0, 119, 182, 0.5);
}

.travel-grid-root {
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 40px; /* Ù‚Ù„Ù„Ù†Ø§ Ø§Ù„Ù…Ø³Ø§ÙØ© Ù„ØªÙˆØ§Ø²Ù† Ø£ÙØ¶Ù„ */
  padding: 20px;
}

.card {
  background: var(--card-bg);
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
  transform: translateY(-6px);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.15);
}

.card .img-wrap {
  position: relative;
  height: 160px;
  overflow: hidden;
  border-radius: 12px;
}

.card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.35s ease;
}

.card:hover img {
  transform: scale(1.06);
}

.card-body {
  padding: 12px 14px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.title-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
}

h3 {
  margin: 0;
  font-size: 18px;
  color: var(--text-dark);
}

p.desc {
  margin: 8px 0 12px;
  color: #555;
  font-size: 14px;
  flex: 1;
}

.meta {
  display: flex;
  gap: 10px;
  align-items: center;
  color: #777;
  font-size: 13px;
}

.price {
  font-weight: 700;
  color: var(--primary);
}

.btn-row {
  display: flex;
  gap: 8px;
  margin-top: 12px;
}

.btn {
  flex: 1;
  padding: 8px 10px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn.book {
  background: var(--primary);
  color: var(--text-light);
}

.btn.details {
  background: var(--secondary);
  color: var(--text-dark);
}

.btn.share {
  background: var(--highlight);
  color: var(--text-light);
}

.actions-top {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  gap: 6px;
}

.icon-btn {
  background: rgba(0, 0, 0, 0.45);
  color: #fff;
  border: none;
  padding: 6px;
  border-radius: 6px;
  cursor: pointer;
}

@media (max-width: 420px) {
  .card .img-wrap {
    height: 140px;
  }
  h3 {
    font-size: 16px;
  }
  .section-head {
    flex-direction: column;
    gap: 15px;
  }
}
