.contact-section {
  padding: 60px 20px;
  background-color: var(--bg-light);
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
  max-width: 400px;
}

.contact-info .title {
  font-size: 28px;
  font-weight: bold;
  color: var(--text-dark);
  margin-bottom: 10px;
}

.contact-info .subtitle {
  font-size: 16px;
  font-weight: 400;
  color: var(--text-muted);
}

.contact-info p {
  font-size: 15px;
  color: var(--text-dark);
  margin: 4px 0;
}

.contact-flex {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 40px;
  flex-wrap: wrap;
  margin-top: 40px;
}

.contact-flex iframe {
  border: none;
  border-radius: 12px;
  width: 100%;
  max-width: 600px;
  height: 300px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
}

.contact-flex form {
  flex: 1;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.contact-flex input,
.contact-flex textarea {
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 14px;
}

.contact-flex button {
  padding: 10px 15px;
  border-radius: 8px;
  border: none;
  background-color: var(--primary);
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.contact-flex button:hover {
  background-color: rgb(29, 29, 241);
  transform: translateY(-3px);
}

/* Responsive */
@media (max-width: 767px) {
  .contact-flex {
    flex-direction: column;
    gap: 30px;
  }
}
