.offers {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  row-gap: 40px;
  padding: 20px;
}

.carde {
  width: 340px;
  text-align: center;
  line-height: 1.6;
  padding: 15px;
  transition: all 0.3s ease;
  background-color: var(--card-bg); /* Ø®Ù„ÙÙŠØ© Ù…ØªÙ†Ø§Ø³Ù‚Ø© */
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  cursor: pointer;
}

.carde:hover {
  transform: translateY(-11px);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.15);
}

.image {
  position: relative;
  transition: all 0.3s ease;
  border-radius: 12px;
  overflow: hidden;
}

.carde:hover .image::before {
  background-color: rgba(0, 0, 0, 0.35); /* Ø£Ø®Ù Ù…Ù† Ø§Ù„Ø³Ø§Ø¨Ù‚ */
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 12px;
}

.carde h2 {
  font-size: 22px;
  margin-top: 14px;
  font-weight: bold;
  color: var(--text-dark);
}

.carde p {
  font-weight: 400;
  color: #555;
}

.carde button {
  width: 90%;
  margin: auto;
  padding: 10px;
  border-radius: 8px;
  font-weight: bold;
  color: var(--text-light);
  background-color: var(--secondary); /* Ø£Ø²Ø±Ø§Ø± CTA */
  transition: all 0.3s ease;
}

.carde button:hover {
  background-color: var(--highlight); /* Ø¹Ù†Ø¯ Hover ÙŠØªØºÙŠØ± Ù„Ù„Ù€ Highlight */
  transform: translateY(-3px);
}

.card-actions {
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  gap: 20px;
  transition: all 0.3s ease;
}

.carde:hover .card-actions {
  opacity: 1;
}

.card-actions button {
  border: none;
  padding: 12px;
  font-size: 17px;
  font-weight: bold;
  background-color: var(--primary);
  color: var(--text-light);
  transition: all 0.3s ease;
  border-radius: 50%;
}

.card-actions button:hover {
  background-color: var(--hover);
  transform: translateY(-4px);
  outline: none;
}

/* Responsive */
@media (max-width: 767px) {
  .offers {
    flex-direction: column;
    align-items: center;
  }
}
