.services-cards-root {
  padding: 20px;
}

.services-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 24px;
}

.services-head .title {
  font-size: 22px;
  font-weight: 700;
  color: var(--primary);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 20px;
}

.service-card {
  background: var(--card-bg);
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  transition: transform 0.3s, box-shadow 0.3s;
}

.service-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.15);
}

.media {
  height: 140px;
  overflow: hidden;
  border-radius: 12px 12px 0 0;
}

.media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.35s ease;
}

.service-card:hover .media img {
  transform: scale(1.06);
}

.body {
  padding: 12px 14px;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.subtitle {
  color: #6b7280;
  font-size: 13px;
}

.desc {
  color: var(--text-dark);
  font-size: 14px;
  flex: 1;
}

.price-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: var(--primary);
}

.btn-row {
  display: flex;
  gap: 8px;
  margin-top: 12px;
}

.btn {
  flex: 1;
  padding: 8px 10px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.btn.primary {
  background: var(--primary);
  color: var(--text-light);
}

.btn.primary:hover {
  background-color: rgb(22, 22, 117);
}

.btn.ghost {
  background: #f3f4f6;
  color: var(--text-dark);
}

.btn.ghost:hover {
  background-color: #e5e7eb;
}

@media (max-width: 420px) {
  .media {
    height: 120px;
  }

  .services-head {
    flex-direction: column;
    gap: 12px;
  }
}
