.testimonial-section {
  padding: 60px 20px;
  background-color: var(--bg-light);
  text-align: center;
}

.title-header {
  font-weight: bold;
  font-size: 28px;
  margin-bottom: 40px;
  color: var(--text-dark);
}

.testimonial {
  background-color: var(--primary);
  color: white;
  border-radius: 14px;
  padding: 30px 20px;
  line-height: 1.6;
  font-size: 16px;
  position: relative;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  max-width: 400px;
  margin: auto;
}

.testimonial:hover {
  transform: translateY(-6px);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.2);
}

.testimonial img {
  border-radius: 50%;
  background-color: white;
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-bottom: 16px;
}

.stars {
  color: #ffc107; /* Ù„ÙˆÙ† Ø§Ù„Ù†Ø¬ÙˆÙ… Ø¨Ø±ØªÙ‚Ø§Ù„ÙŠ Ø§Ø­ØªØ±Ø§ÙÙŠ */
  font-size: 22px;
  margin-top: 12px;
  display: inline-block;
}

.testimonial p {
  margin: 10px 0 0 0;
  font-size: 15px;
}

/* Responsive */
@media (max-width: 420px) {
  .testimonial {
    padding: 20px 15px;
  }
  .testimonial img {
    width: 80px;
    height: 80px;
  }
  .title-header {
    font-size: 22px;
  }
}
